:root {
    --delay: 0s;
}

.main-container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 25px;
    overflow: auto;
    padding-left: 0;
}

.main-container * {
    color: #9b9ca7;
}

.user-box {
    margin-top: 25px;
    display: flex;
}

.user-box+.user-box {
    margin-top: 20px;
}