.wrapper {
	max-width: 950px;
	min-height: 550px;
	margin: 50px auto;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	padding: 8px;
}

.majburiy-fanlar, .fanlar-toplami, .test-tili {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}

#til-selectori {
	display: none;
	width: 100px;
}

/*----------*/
h2 {
	font-size: 20px;
	/* color: #0C4292; */
    color: white;
	font-family: Verdana;
}

.test-yili {
	width: 100%;
	height: 80px;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: space-between;
}

.menu-of-years {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	position: relative;
}

.menu-of-years > .tanlangan-yil {
	/* color: #0C4292; */
    color: white;
	/* border-bottom: 2px solid #0C4292; */
    border-bottom: 2px solid white;
}

.menu-of-years>li {
	list-style: none;
	font-size: 24px;
	font-family: Verdana;
	/* color: #839AC9; */
    color: white;
	cursor: pointer;
}


/*----------------*/

.majburiy-fanlar {
	width: 100%;
	min-height: 55px;
}

.majburiy-fanlar > p {
	font-size: 20px;
	color: #0C4292;
	font-family: fangsong;
}

/*-----------------*/

.fanlar-toplami {
	width: 100%;
	height: 85px;
}

select {
	width: 100%;
	border: 1px solid #0E5CAD;
	border-radius: 30px;
	font-size: 18px;
	color: #0E5CAD;
	padding: 10px;
	display: flex;
	/*-webkit-appearance: none;
  	-moz-appearance: none;
  	appearance: none;*/
  	position: relative;
  	outline: none;
}

option {

}

option[selected] {
	font-size: 18px;
	color: #0C42928f;
	margin-bottom: 10px;
	position: relative;
}

.test-tili {
	width: 100%;
	height: 85px;
}

/*-------------*/

.test-vaqti {
	height: 70px;
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	border-bottom: 1.5px solid #5177B0;
}

.test-vaqti > h2 {
	width: 100%;
	font-size: 20px;
	font-family: Verdana;
}

.test-vaqti > p {
	font-size: 20px;
	font-family: fangsong;
	color: #0C4292;
}

.boshlash-btn {
	width: 160px;
	height: 40px;
	border: none;
	background: none;
	color: white;
	border-radius: 30px;
	background-color: #3F51B5;
	text-transform: uppercase;
	cursor: pointer;
}

@media all and (max-width:550px) {
	.test-yili {
		flex-wrap: nowrap;
	}

	.menu-of-years {
		display: none;
	}

	#til-selectori {
		display: block;
		height: 40px;
		outline: none;
		border: none;
	}


}

select::part(listbox) {
	margin-top: 20px;
}

select::-webkit-part(button) {
		border-radius: 10px;
		background-color: red;
		padding: 20px;
	}