.app-settings
{
	width: 100%;
	height: 100%;
	/* border: 1px solid red; */
}

.app-settings > .app-settings-menu
{
	margin: auto;
	width: 85%;
	height: 75px;
	/* border: 2px solid green; */
	display: flex;
	align-items: center;
	column-gap: 40px;

}

.app-settings > .app-settings-menu .title
{
	color: white;
	position: relative;
}

.app-settings > .app-settings-menu .title.active::after {
	content: '';
	position: absolute;
	height: 5px;
	width: 60px;
	background-color: #5e38cc;
	bottom: -5px;
	left: 0;
}

.app-settings > .app-settings-header
{
	width: 100%;
	height: 100%;
	/* border: 1px solid red; */
	overflow: scroll;
}
