.user-box+.user-box .cards-wrapper {
    margin-left: 0;
    flex-grow: 1;
    max-width: none;
}

.user-box+.user-box .today {
    margin-left: 30px;
}

.user-box+.user-box .cards-header {
    padding: 20px 30px;
    justify-content: space-between;
}

.user-box .cards-header,
.user-box .cards-view {
    display: flex;
    align-items: center;
    justify-content: center;
}

.user-box .cards-header svg,
.user-box .cards-view svg {
    width: 24px;
}

.user-box .cards-header .title,
.user-box .cards-view .title {
    margin: 0 16px;
    font-size: 15px;
}

.user-box .cards-header-date svg,
.user-box .cards-view-date svg {
    width: 30px;
}

.user-box .cards-header .cards-button,
.user-box .cards-view .cards-button {
    background: #15193c;
    padding: 10px 16px;
    margin-right: 0;
}

.user-box .cards-header .cards-button svg,
.user-box .cards-view .cards-button svg {
    width: 16px;
    margin: 0 2px 0 0;
}

.user-box .cards-header .date-wrapper,
.user-box .cards-view .date-wrapper {
    display: flex;
    align-items: center;
    margin: auto;
}

.user-box .cards-header .date-wrapper .title,
.user-box .cards-view .date-wrapper .title {
    margin: 0 16px;
}

.cards-view>svg {
    margin-right: 12px;
}

.today {
    position: relative;
}

.today:before {
    content: "";
    position: absolute;
    width: 5px;
    height: 5px;
    background-color: #ef415c;
    bottom: -8px;
    right: 50%;
    border-radius: 50%;
}

.card {
    background: #1a2049;
    background: radial-gradient(circle, rgba(26, 32, 73, 1) 0%, rgba(19, 22, 47, 1) 100%);
    padding: 40px 30px;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    max-height: 430px;
    width: 100%;
}

.card .title {
    font-size: 16px;
    font-weight: 500;
}

.card .subtitle {
    font-size: 13px;
    line-height: 1.6em;
}

.card+.card {
    margin-left: 20px;
}

.activity {
    max-height: 100%;
}

.activity .title {
    margin-bottom: 20px;
}

.activity-links {
    display: flex;
    align-items: center;
    margin-top: auto;
    font-size: 15px;
}

.activity-link {
    padding-bottom: 10px;
    position: relative;
    cursor: pointer;
    transition: 0.3s;
}

.activity-link+.activity-link {
    margin-left: 25px;
}

.activity-link+.activity-link:before {
    content: "";
    position: absolute;
    width: 5px;
    height: 5px;
    background-color: #ef415c;
    top: -2px;
    right: -8px;
    border-radius: 50%;
}

.activity-link+.activity-link:hover:after {
    content: "";
    position: absolute;
    width: 22px;
    height: 2px;
    background: #4255d4;
    left: 0;
    bottom: 0;
}

.activity-link+.activity-link:hover {
    color: #bebec4;
    -webkit-text-stroke: 0.3px;
}

.activity-link.active {
    color: #bebec4;
    font-weight: 500;
}

.activity-link.active:before {
    content: "";
    position: absolute;
    width: 22px;
    height: 2px;
    background: #4255d4;
    left: 0;
    bottom: 0;
}

.destination {
    display: flex;
    align-items: center;
    margin-top: auto;
}

.destination-card {
    background: #1a1d3a;
    background: linear-gradient(45deg, rgba(26, 29, 58, 1) 0%, rgba(33, 39, 82, 1) 100%);
    padding: 20px;
    width: 100%;
    border-radius: 6px;
}

.destination-card+.destination-card {
    margin-left: 20px;
    background: #1a1d3a;
    background: linear-gradient(325deg, rgba(26, 29, 58, 1) 0%, rgba(33, 39, 82, 1) 100%);
}

.destination-profile {
    display: flex;
    align-items: center;
    font-size: 14px;
}

.destination-profile svg {
    width: 18px;
    flex-shrink: 0;
    margin-right: 8px;
}

.destination-length {
    margin-left: auto;
    display: flex;
    align-items: center;
    font-size: 13px;
}

.destination-points {
    margin-top: 30px;
}

.profile-img {
    width: 46px;
    height: 46px;
    object-fit: cover;
    border-radius: 50%;
    padding: 2px;
    border: 1px solid #bebec4;
    flex-shrink: 0;
}

.point {
    font-size: 14px;
    color: #bebec4;
    font-weight: 500;
}

.sub-point {
    font-size: 13px;
    margin-top: 4px;
}

.discount {
    max-width: 320px;
    width: 100%;
    max-height: 100%;
}

.discount .title {
    margin-bottom: 30px;
}

.discount .subtitle {
    margin-bottom: 8px;
}

.discount .subtitle-count {
    font-size: 17px;
    color: #17a98a;
    font-weight: 500;
}

.discount .subtitle-count+.subtitle {
    margin-top: 20px;
}

.discount .subtitle-count.dist {
    color: #e85471;
}

.discount-wrapper {
    display: flex;
}

.discount-chart {
    margin-left: auto;
}

.discount-profile {
    display: flex;
    align-items: center;
    margin-top: 40px;
}

.discount-img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    object-fit: cover;
    margin: 0 16px 0 12px;
}

.discount-name {
    font-weight: 500;
    font-size: 15px;
}

.discount-type {
    font-size: 13px;
    margin-top: 4px;
}

.circle {
    width: 100px;
    height: 100px;
    border: 3px solid #5b5f78;
    border-radius: 50%;
    position: relative;
}

.pie {
    position: relative;
    width: 120px;
    height: 120px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) rotate(-90deg);
}

.pie svg circle {
    fill: transparent;
    stroke: #4356d6;
    stroke-width: 14;
    stroke-dasharray: 275;
    stroke-dashoffset: 235;
    animation: pieChart 3.8s linear forwards;
}

@keyframes pieChart {
    100% {
        stroke-dashoffset: 0;
    }
}

.counter {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: #3e50c5;
    font-weight: 500;
}

.offer-button {
    background: #4255d4;
    padding: 14px;
    text-align: center;
    justify-content: center;
    margin-top: auto;
    margin-right: 0;
    color: #fff;
    font-size: 13px;
    cursor: pointer;
}

.cards-header {
    background: #10122b;
    border-radius: 6px 6px 0 0;
    padding: 20px 45px;
    font-size: 14px;
    font-weight: 500;
}

.cards-header-date {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.cards-header-date svg {
    color: #4154d1;
    background: #14183c;
    border-radius: 50%;
    padding: 5px;
    width: 30px;
}

.cards-wrapper {
    margin-left: 20px;
    max-width: 305px;
    width: 100%;
}

.cards-hour {
    font-size: 26px;
}

.cards-hour .am-pm {
    font-size: 15px;
    font-weight: 500;
}

.cards.card {
    border-radius: 0 0 6px 6px;
    padding: 30px 20px;
}

.cards.card svg {
    width: 24px;
    margin-left: auto;
    color: #818394;
}

.cards-head {
    display: flex;
    align-items: center;
}

.degree {
    margin-top: 10px;
    font-size: 13px;
    display: flex;
    align-items: center;
    color: #818394;
    font-weight: 500;
}

.degree svg {
    width: 24px;
    margin-right: 12px;
}

.items {
    display: flex;
    flex-wrap: wrap;
    margin-top: 16px;
    font-size: 13px;
    font-weight: 500;
}

.items.numbers {
    margin-top: 0;
}

.item {
    flex: 0 1 calc(100% / 7);
    padding: 10px 5px;
    text-align: center;
}

.item.is-active {
    background: #4255d4;
    border-radius: 50%;
    color: #fff;
}

.item.disable {
    color: #595b5b;
}

.account {
    width: 100%;
    height: 180px;
    margin-top: auto;
    flex-grow: 0;
    position: relative;
    transition: 0.3s;
    cursor: pointer;
    transition: 0.3s;
}

.account:hover {
    transform: scale(1.02);
}

.account:before {
    content: "";
    position: absolute;
    width: 24px;
    height: 24px;
    box-shadow: -15px 0 0 0 #ef8741;
    background: #ef415c;
    top: 20px;
    left: 42px;
    border-radius: 50%;
}

.account-wrapper {
    max-width: 310px;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    align-items: center;
    border: 3px solid #1a2049;
    border-radius: 6px;
}

.account-profile {
    margin: auto;
    position: relative;
    text-align: center;
    position: relative;
}

.account-profile img {
    width: 84px;
    height: 84px;
    border-radius: 50%;
    object-fit: cover;
    object-position: left;
    border: 3px solid #4255d3;
    padding: 5px;
}

.account-profile .blob {
    position: absolute;
    border-radius: 50%;
    animation: fly 5.8s linear infinite alternate;
}

.account-profile .blob:nth-child(1) {
    width: 14px;
    height: 14px;
    top: 25px;
    left: -20px;
    background: #28327a;
    animation-delay: 0.9s;
}

.account-profile .blob:nth-child(2) {
    width: 18px;
    height: 18px;
    background: #87344c;
    right: -20px;
    top: -20px;
    animation-delay: 0.2s;
}

.account-profile .blob:nth-child(3) {
    width: 12px;
    height: 12px;
    background: #13645b;
    right: -35px;
    top: 50%;
    animation-delay: 1.8s;
}

.account-name {
    margin: 20px 0 10px;
}

.account-title {
    font-size: 14px;
}

.account-cash {
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 6px;
    padding-top: 16px;
    position: relative;
}

.account-cash:before {
    content: "";
    position: absolute;
    width: 5px;
    height: 5px;
    background: #9b9ca7;
    right: 10px;
    border-radius: 50%;
    box-shadow: -10px 0 0 0 #9b9ca7, 10px 0 0 0 #9b9ca7;
    top: 24px;
}

.account-income {
    font-size: 14px;
}

.account-iban {
    margin-top: auto;
    font-weight: 500;
}