.messages-section {
    flex-shrink: 0;
    background-color: var(--projects-section);
    margin-left: 18px;
    flex: 1;
    width: 100%;
    border-radius: 30px;
    position: relative;
    overflow-y: auto;
    transition: all 300ms cubic-bezier(0.19, 1, 0.56, 1);
}

.messages-section .messages-close {
    position: absolute;
    top: 12px;
    right: 12px;
    z-index: 3;
    border: none;
    background-color: transparent;
    color: var(--main-color);
    display: none;
}

.messages-section.show {
    transform: translateX(0);
    opacity: 1;
    margin-left: 0;
}

.messages-section .projects-section-header {
    position: sticky;
    top: 0;
    z-index: 1;
    margin: 0;
    padding: 20px 24px 10px 24px;
    border-bottom: 1px solid var(--message-box-border);
    background-color: var(--project-section-blur-bg);
    backdrop-filter: blur(5px);
}