*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

:root {
  --clr-primary: #1e1d4b;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

h1,
h2 {
  font-family: "Merriweather", serif;
  line-height: 1.1;
}

h1 {
  font-size: 2.6rem;
}

h2 {
  font-size: 1.8rem;
  margin-block-end: calc(var(--space) + 0.5rem);
}

ul {
  padding: 0;
  list-style-type: none;
}

input:is([type="checkbox"], [type="radio"]) {
  display: none;
}

/* - */
.flow {
  display: flex;
  gap: var(--space);
  flex-direction: column;
}
/* = */

.dashboard {
  --space: 1rem;

  display: grid;
  gap: 3rem;
  width: min(100% - 4rem, 40rem);
  padding: 5rem;
  background: #ffffff;
  box-shadow: 0px 20px 37px rgba(26, 24, 108, 0.1);
  border-radius: 24px;
}

.switches p {
  font-size: 1.6rem;
}

.switch-field {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.switch {
  display: inline-flex;
  align-items: center;
  padding-inline: 0.75rem;
  width: 6rem;
  height: 3.5rem;
  background: #e9e9fb;
  border-radius: 25rem;
  cursor: pointer;
}

.switch span {
  position: relative;
  display: inline-block;
  width: 2.25rem;
  height: 2.25rem;
  background: #ffffff;
  /*   border: 1px solid #a4a4df; */
  border-radius: inherit;
  transition: left 0.25s ease;
  left: 0;
}
.switch input:checked + span {
  border-color: var(--clr-primary);
  background-color: var(--clr-primary);
  left: 2.25rem;
}

ul li {
  display: flex;
  gap: var(--space);
  align-items: center;
}

.custom-radio {
  display: block;
  cursor: pointer;
}

.custom-radio span {
  display: block;
  /* vertical-align: middle; */
  width: 2.25rem;
  height: 2.25rem;
  box-sizing: initial;
  background: #fff;
  border: 1px solid #a4a4df;
  border-radius: 25rem;
}
.custom-radio input:checked + span {
  border-color: var(--clr-primary);
  background: var(--clr-primary);
  box-shadow: inset 0 0 0 4px white;
}

a {
  color: inherit;
}
a:hover {
  text-decoration: none;
}

.chatSettings
{
  width: 100%;
  height: 90%;
  display: flex;
  align-items: center;
  column-gap: 30px;
  /* border: 1px solid red; */
  justify-content: center;
}